body {
    background-color: cornsilk;
    font-family: Arial, sans-serif;
}

.container-fluid {
    margin: 20px 0px;
}

.row {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: 0 auto;
}

.row > div {
    margin: 10px 20px;
}

.headingLabel {
    font-size: 18px;
    font-weight: bold;
    margin: 5px 10px;
}

.headingInput {
    border: 1px solid rgb(0, 0, 0, .4);
    width: 50px;
    max-width: 50px;
    height: 30px;
    padding: 0;
    text-align: center;
    font-size: 18px;
}

.headingBtn {
    padding: 8px 8px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    height: 34px;
    background-color: lightseagreen;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    /* <-- Apparently some margin are still there even though it's hidden */
    margin: 0; 
}

input[type=number] {
    /* Firefox */
    -moz-appearance:textfield; 
}

.gameBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 30px;
}

.status {
    margin-bottom: 30px;
    font-size: 18px;
}

.board-row {
    display: flex;
    flex-direction: row;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.sbg {
    background: blanchedalmond;
}

.square:focus {
    outline: none;
}

.historyDiv {
    display: flex;
    flex-direction: column;
}

.historyBtn {
    width: 300px;
    font-size: 14px;
    padding: 8px 8px;
    margin-bottom: 5px;
    color: white;
    cursor: pointer;
    font-weight: bold;
    height: 40px;
    background-color: coral;
}

.historyBtnActive {
    background-color: dimgray;
}

.sortHistory {
    background-color: gold;
    color: black;
}